*{margin: 0;padding: 0;font-family: 'Noto Sans KR', sans-serif;}
li{list-style: none;}
a{text-decoration: none;color: black;cursor: pointer;}



/* ********************* header ***************************** */


.header{
    display: flex;
    width: 100%;
    height: 100px;
    line-height: 100px; 
    justify-content: space-between;   
}


.header .logo a{
    font-size: 0px;
    display: block;
    width: 140px;
    background-image: url(../img/logo_navy.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 58%;
}

.header .gnb ul{
    display: flex;
}

.header .gnb ul li a{
    padding: 0 20px;
    font-size: 15px;
    font-weight: 700;
}

.header .gnb ul li:last-child a{
    border-left: 1px solid black;
}


.header .icon ul{
    display: flex;
}

.header .icon{margin-right: 20px;}
.header .icon ul li{margin: 0 20px;}




/* ********************* wrap ***************************** */

.wrap{
    width: 830px;
    margin: 50px auto 0;
    display: flex;
}

/* ********************* visual_main ***************************** */


.wrap .visual_main img{
    display: block;
    width: 400px;
}



/* ********************* login_form ***************************** */


.wrap .login{
    width: 400px;
    margin-left: 30px;
}

.wrap .login .title{
    text-align: center;
    font-size: 30px;
    margin: 10px 0 20px 0; 
}


.wrap .login .hr{
    border-bottom: 1px solid #000;
}

.wrap .login .login_form{
    margin-top: 30px;
}

.wrap .login .login_form .pw_hidden{
    position: relative;
}

.wrap .login .login_form .pw_hidden i{
    position: absolute;
    right: 15px;
    top: 18px;
}


/* ********************* login_input ***************************** */

.wrap .login .login_form .user_id,
.wrap .login .login_form .user_pw{
    width: 100%;height: 50px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    padding-left: 15px;
    font-size: 15px;
}

.wrap .login .login_form .user_chk{ 
    width: 15px;
    height: 15px;
    margin-top: 10px;
}

.wrap .login .login_form .user_chk + label{
    font-size: 15px;
    color: #494949;
}


/* ********************* login_btn ***************************** */


.wrap .login .login_form .login_btn{
    margin-top: 30px;
}

.wrap .login .login_form .login_btn ul li a button{
    width: 100%;height: 50px;
    border: 1px solid rgb(0,32,83);
    margin-bottom: 10px;
    font-size: 15px;
    letter-spacing: 2px;
    border-radius: 30px;
    font-weight: bold;
    cursor: pointer;
}

.wrap .login .login_form .login_btn ul li:nth-child(1) a button{
    background-color: rgb(0,32,83);
    color: white;

}

.wrap .login .login_form .login_btn ul li:nth-child(2) a button{
    background-color: white;
}



/* ********************* login_nav ***************************** */

.wrap .login .login_form .login_nav{
    margin-top: 20px;
}
.wrap .login .login_form .login_nav ul{
    display: flex;
    justify-content: center;
    align-items: center;
}

.wrap .login .login_form .login_nav ul li{
    font-size: 13px;
}


.wrap .login .login_form .login_nav ul li:nth-child(2)::before,
.wrap .login .login_form .login_nav ul li:nth-child(3)::before{
    background-color: #ccc;
    width: 1px;
    height: 14px;
    margin: 0 10px;
    display: inline-block;
    content: '';
}

/* ********************* login_sns ***************************** */

.wrap .login .login_form .login_sns{
    margin-top : 30px;
}


.wrap .login .login_form .login_sns h4 {
    display: flex;
    align-items: center;
    font-size: 12px;
  }

.wrap .login .login_form .login_sns h4::before,
.wrap .login .login_form .login_sns h4::after {
    content: "";
    background: rgba(0, 0, 0, 0.35);
    height: 1px;
    flex-grow: 2;
    margin: 0px 16px;
  }

  .wrap .login .login_form .login_sns ul {
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
  }

  .wrap .login .login_form .login_sns ul li img{
    width: 50px;
  }